<script>
import axios from "axios";
const host = process.env.VUE_APP_SERVER_HOST;
const port = process.env.VUE_APP_SERVER_PORT;

const regionsById = [
  {
    id: "sev-kaz",
    name: "Северо-Казахстанская область",
    branch_id: 4800,
    zoom: 50,
  },
  {
    id: "akm",
    name: "Акмолинская область",
    branch_id: 300,
  },
  {
    id: "kost",
    name: "Костанайская область",
    branch_id: 3900,
  },
  {
    id: "pavl",
    name: "Павлодарская область",
    branch_id: 4500,
  },
  {
    id: "akt",
    name: "Актюбинская область",
    branch_id: 600,
  },
  {
    id: "zap-kaz",
    name: "Западно-Казахстанская область",
    branch_id: 2700,
  },
  {
    id: "atr",
    name: "Атырауская область",
    branch_id: 1500,
  },
  {
    id: "man",
    name: "Мангистауская область",
    branch_id: 4300,
  },
  {
    id: "kyz",
    name: "Кызылординская область",
    branch_id: 3300,
  },
  {
    id: "uly",
    name: "Улытауская область",
    branch_id: 7200,
  },
  {
    id: "turk",
    name: "Туркестанская область",
    branch_id: 5800,
  },
  {
    id: "alm",
    name: "Алматинская область",
    branch_id: 900,
  },
  {
    id: "kar",
    name: "Карагандинская область",
    branch_id: 3000,
  },
  {
    id: "zham",
    name: "Жамбыльская область",
    branch_id: 2100,
  },
  {
    id: "zhet",
    name: "Жетысуская область",
    branch_id: 7000,
  },

  {
    id: "vost-kaz",
    name: "Восточно-Казахстанская область",
    branch_id: 1800,
  },
  {
    id: "abai",
    name: "Абайская область",
    branch_id: 7100,
  },
];
export default {
  components: {},
  data() {
    return {
      serverList: [],
      serverInfo: {
        online: 0,
        total: 0,
      },
      ticketInfo: "",
      operatorInfo: "12 операторов в сети",
      averageRate: "",

      selectedRegion: "Все билеты",
      selectRegionId: null,
      currentRegion: null,
      currentColor: "",

      branches: null,
    };
  },
  methods: {
    async getRegionInfo() {
      try {
        this.serverInfo.online = 0;
        this.serverInfo.total = 0;
        const result = await axios.get(
          `http://${host}:${port}/api/v1/tickets/map`,
          {
            headers: {
              bearer: localStorage.getItem("authToken"),
            },
          }
        );

        // console.log(result);
        // this.serverInfo.online = result.data.data.onlineServers;
        // this.serverInfo.total = result.data.data.total;
        this.ticketInfo = result.data.data.tickets;
        this.averageRate = result.data.data.averageRate;
        this.serverList = result.data.data.server_list;
        console.log(this.serverList)
        // const off = result.data.data.offlineBranches;
        console.log(this.serverList);
        this.serverList.map((e) => {
          e.children.map((child) => {
            if (child.ONN === 1) {
              this.serverInfo.online++;
            }
            this.serverInfo.total++;
          });
          const region = regionsById.find(
            (element) => element.branch_id === e.F_ID*1
          );
          
          
         
          if (region) {
            const element = document.getElementById(`${region.id}`);
            for (let i = 0; i < e.children.length; i++) {
              const child = e.children[i];
              if (child.ONN === 0) {
                // console.log("no conn");
                element.style.fill = "red";
                element.querySelector("title").textContent =
                  "Потеряно связь с серверами";
                // break; // Exit the loop when the condition is met
              } else {
                element.style.fill = "#6cb9e6";
              }
            }
            element.addEventListener("click", () =>
              this.selectRegion(region.id)
            );
          }
        });
        if (this.serverList.length === 1) {
          this.selectedRegion = this.serverList[0].F_NAME;
        }

        // regionsById.map(e => {
        //   if (!off.find(el => el.F_ID == e.branch_id)) {
        //     const element = document.getElementById(`${e.id}`);
        //     element.style.fill = "#6cb9e6";
        //     element.addEventListener("click", () => this.selectRegion(e.id));
        //   }
        // })
      } catch (err) {
        console.log(err);
      }
    },
    async selectRegion(region) {
      this.serverInfo.online = 0;
      this.serverInfo.total = 0;
      console.log("Region", region);
      try {
        const select = regionsById.find((e) => region === e.id);
        console.log("Selected founded",select)
        this.selectedRegion = select?.name;
        this.selectRegionId = select?.branch_id;
        const result = await axios.get(
          `http://${host}:${port}/api/v1/tickets/map/${select.branch_id}`,
          {
            headers: {
              bearer: localStorage.getItem("authToken"),
            },
          }
        );

        const server = result.data.data.server_list;
        // const off = result.data.data.offlineBranches;
        // console.log(result.data.data);

        server.children.map((child) => {
          if (child.ONN === 1) {
            this.serverInfo.online++;
          }
          this.serverInfo.total++;
        });

        this.ticketInfo = result.data.data.tickets;
        this.averageRate = result.data.data.averageRate;

        if (this.currentRegion) {
          const previousRegion = document.getElementById(this.currentRegion);

          if (this.currentRegion === region) {
            previousRegion.style.fill = this.currentColor;
            this.currentRegion = null;
            this.selectedRegion = "Все билеты";
            this.selectRegionId = null;
            if (this.serverList.length === 1) {
              if (this.serverList.length === 1) {
                this.selectedRegion = this.serverList[0].F_NAME;
              }
            }
            return;
          }

          previousRegion.style.fill = this.currentColor;
        }

        this.currentRegion = region;

        const mapPart = document.getElementById(region);
        this.currentColor = mapPart.style.fill;
        mapPart.style.width = "300px";
        // console.log(mapPart.transform)
        mapPart.style.fill = "#4FCB1D";
      } catch (err) {
        console.log(err);
      }
    },

    goAmount() {
      this.$router.push({
        path: `/amount-dash`,
        query: { parent_branch: this.selectRegionId },
      });
    },
    goTickets() {
      this.$router.push({
        path: "/tickets",
        query: { branch_id: this.selectRegionId },
      });
    },
  },
  computed: {},
  watch: {
    "$route.query": {
      immediate: true,
      handler(newQuery) {
        if (newQuery.parent_branch) {
          const region = regionsById.find((e) => newQuery.parent_branch*1 === e.branch_id)
          this.selectRegion(region.id);
          console.log("New query",newQuery.parent_branch);
        }
      },
    },
  },
  mounted() {
    this.getRegionInfo();
  },
};
</script>
